<template>
  <div class="login-container">
    <div class="m-c">
      <div class="login-box">
        <img src="~@/assets/images/register-text.png" style="width:142px;margin-left:11px;margin-bottom:26px">
        <div class="box-container">
          <Form ref="formValidate" :model="formValidate" :rules="ruleValidate">
            <FormItem prop="phone_num">
              <Input size="large" v-model="formValidate.phone_num" placeholder="手机">
              <img src="~@/assets/images/shouji.png" slot="prefix" style="width:19px;">
              </Input>
            </FormItem>
            <FormItem prop="password">
              <Input size="large" type="password" v-model="formValidate.password" placeholder="密码">
              <img src="~@/assets/images/password-icon.png" slot="prefix" style="width:19px;">
              </Input>
            </FormItem>

            <FormItem prop="vcode">
              <div class="flex-ac">
                <Input size="large" v-model="formValidate.vcode" style="width:180px" placeholder="短信验证码">
                </Input>
                <Button type="primary" :disabled='sended' class="code-btn"
                  @click.native="getVCode">{{codeBtnText}}</Button>
              </div>
            </FormItem>
            <Button type="primary" class="login-btn" @click.native="register">注 册</Button>
          </Form>
          <div class="flex-je mt-18">
            <router-link to="/login">已有账号？请登录</router-link>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import configUrl from '@/config/index.js'
import md5 from 'js-md5'
export default {
  data () {
    const validatePhone = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请填写手机号码'));
      } else if (/^1[3456789]\d{9}$/.test(value)) {

        callback();
      } else {
        callback(new Error('请正确输入手机号'));
      }
    };
    const valitateVcode = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请填写验证码'));
      } else {
        callback();
      }
    };
    return {
      sended: false,
      sendTimer: null,
      time: 60,
      codeBtnText: '发送短信验证码',
      formValidate: {
        phone_num: '',
        password: '',
        vcode: ''
      },
      ruleValidate: {
        phone_num: [
          { validator: validatePhone, trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 16, message: '请输入6-16位密码', trigger: 'blur' },
        ],
        vcode: [{ validator: valitateVcode, trigger: 'blur' }]
      }
    }
  },
  methods: {
    async register () {
      this.$refs['formValidate'].validate(async (valid) => {
        if (valid) {
          let params = {
            phone_num: this.formValidate.phone_num,
            passwd: md5(this.formValidate.password),
            vcode: this.formValidate.vcode
          }
          let resData = await this.$post('cgi/signup', params)

          if (resData.ret_code >= 0) {
            this.$router.push('/login')
          }
        }
      })

    },
    getVCode () {
      this.$refs['formValidate'].validateField('phone_num', (valid) => {
        if (!valid) {
          let params = {
            phone_num: this.formValidate.phone_num
          }
          clearInterval(this.sendTimer)
          this.$post(`${configUrl.codeUrl}/cgi/get_code`, params, false).then((resData) => {
            if (resData.ret_code == 0) {
              this.sended = true
              this.sendTimer = setInterval(() => {
                this.time--
                this.codeBtnText = `${this.time}s后 重新发送`
                if (this.time == 0) {
                  clearInterval(this.sendTimer)
                  this.sended = false
                  this.codeBtnText = '发送短信验证码'
                  this.time = 60
                }
              }, 1000)
            }
          }).catch(() => {
            this.sended = false
          })
        }
      })
    }
  }
}
</script>
<style lang="less" scoped>
.login-container {
  background: url(~@/assets/images/bj.png) no-repeat center center fixed;
  background-size: cover;
  .login-btn {
    width: 100%;
    height: 48px;
    font-size: 20px;
  }
  .m-c {
    max-width: 1200px;
    position: relative;
    margin: auto;
    height: 100vh;
    .login-box {
      position: absolute;
      right: 50px;
      height: 100vh;
      display: flex;
      flex-direction: column;
      justify-content: center;
      .box-container {
        width: 400px;
        height: 473px;
        border-radius: 10px;
        background-color: #fff;
        padding: 33px 20px;
      }
      .code-btn {
        height: 41px;
        border-radius: 20px;
        margin-left: 15px;
      }
    }
  }
}
/deep/ .ivu-input-large {
  height: 48px;
}
/deep/ .ivu-input {
  background-color: #eeeeee;
  border: none;
}
/deep/ .ivu-input-with-prefix {
  padding-left: 41px;
}
/deep/ .ivu-input-prefix,
/deep/ .ivu-input-suffix {
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>